@import url(../../vendor/normalize.css);
@import url(../../fonts/inter/Inter_Web/inter.css);

.search {
  width: 100%;
  max-width: 1280px;
  padding: 80px 70px 10px;
  margin: auto;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}

.search__wrapper {
  display: flex;
}

.search__form {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  border-radius: 8px;
  margin-top: 8px;
  margin-bottom: 27px;
  background: #F9F9F9;
}

.search__form-input {
  width: 100%;
  height: 100%;
  background: #F9F9F9;
  border-radius: 8px;
  border: none;
  padding-left: 20px;
  font-size: 14px;
  line-height: 18px;
  color: #A0A0A0;
}

.search__input {
  width: 100%;
  padding: 0;
  margin-left: 10px;
  border: none;
  outline: none;
  font-size: 14px;
  line-height: 18px;
  font-weight: 500;
  text-align: start;
  color: #A0A0A0;
  background: #F9F9F9;
}

.search__input::placeholder {
  transition: opacity .3s;
  color: #A0A0A0;
}

.search__input:focus::placeholder {
  opacity: .7;
}

.search__button {
  color: #fff;
  background: #3456F3;
  border: none;
  border-radius: 8px;
  margin-top: 10px;
  outline: none;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
  letter-spacing: 0px;
  margin-left: 10px;
  transition: opacity .3s;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 44px;
  height: 44px;
  background-image: url("../../images/searrch-btn.png");
  background-repeat: no-repeat;
  background-position: center;
}

.search__button:hover {
  opacity: .8;
  cursor: pointer;
}

.search__line {

  background: #E8E8E8;
  width: 100%;
}

@media screen and (max-width: 1023px) {
  .search {
    padding: 70px 30px 80px;
  }

  .search__input {
    margin-left: 25px;
  }

  .search__form {
    margin-bottom: 26px;
  }
}

@media screen and (max-width: 768px) {
  .search {
    padding: 70px 14px 10px;
  }

  .search__form {
    margin-bottom: 29px;
  }

  .search__icon {
    display: none;
  }

  .search__form-input {
    width: 100%;
    padding: 0;
    margin-left: 12px;
    border: none;
    outline: none;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 22px;
    text-align: left;
  }

  .search__form {
    margin-bottom: 30px;
  }

  .search__button {
    border: none;
    border-radius: 5px;
    margin: 10px 6px 6px 0;
    padding: 13px 15px;
    outline: none;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 18px;
    letter-spacing: 0px;
    text-align: center;
    margin-left: 21px;
    transition: opacity .3s;
  }
}

@media screen and (max-width: 320px) {
  .search__form {
    width: 100%;
    height: 44px;
  }


}
