.filter-checkbox {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 12px;
    padding-bottom: 39px;
    border-bottom:  1.5px rgba(170, 170, 170, .2) solid;
}

.filter-checkbox__switch {
    position: relative;
    display: inline-block;
    width: 36px;
    height: 20px;
}

.filter-checkbox__switch input {
    display:none;
}

.filter-checkbox__slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #2BE080;
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 34px;
}

.filter-checkbox__slider:before {
    position: absolute;
    content: "";
    height: 8px;
    width: 8px;
    left: 6px;
    bottom: 6px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 50%;
}

input:checked + .filter-checkbox__slider {
    background-color: #2BE080;
}

input:focus + .filter-checkbox__slider {
    box-shadow: 0 0 1px #2BE080;
}

input:checked + .filter-checkbox__slider:before {
    -webkit-transform: translateX(18px);
    -ms-transform: translateX(18px);
    transform: translateX(18px);
}

.filter-checkbox__text {
    padding: 0;
    margin: 0;
    font-size: 13px;
    line-height: 18px;
    color: #000000;
}

@media screen and (max-width: 767px) {
    .filter-checkbox__text {
        font-size: 11px;
        line-height: 18px;
    }

    .filter-checkbox {
        padding-bottom: 63px;
    }
}
